import { useState, useEffect, useCallback, useRef } from 'react'
import { inject, observer } from 'mobx-react'
import Icon from '../../components/Icon/Icon'
import './rebate.less'
import { EVENT_TRANS_BEGIN, EVENT_TRANS_END } from '../../utils/Constants'
import { bg } from '../../utils/utils'
import useContract from '../../hooks/useContractLp'
import { BigNumber } from 'ethers'
import axios from 'axios'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import classNames from 'classnames'
import { show } from 'react-functional-modal'
import Register from './Register'
import useLang from '../../hooks/useLang'
import WalletView from '../../components/Header/WalletView'
import Emitter from '../../utils/Emitter'
import Spinner from '../../components/Spinner/Spinner'
import { useModal } from 'react-hooks-use-modal'
function Rebate({ wallet, intl }) {
  const lang = useLang(intl.dict, 'header');
  const [accountType, setAccountType] = useState("broker")
  const [brokerInfos, setBrokerInfos] = useState({})
  const [updatedTime, setUpdatedTime] = useState("");
  const [reBateInfo, setReBateInfo] = useState({})
  const [claimed, setClaimed] = useState(0)
  const [unclaimed, setUnclaimed] = useState(0)
  const [isCopied, setIsCopied] = useState(false)
  const [totalUser, setTotalUser] = useState("")
  const [explained, setExplained] = useState('')
  const [totalFee, setTotalFee] = useState('')
  const [isClaiming, setIsClaiming] = useState(false)
  const [RegisterModal, openRegisterModal, closeRegisterModal] = useModal("root", {
    preventScroll: true,
    closeOnOverlayClick: false
  })

  const infosRef = useRef()

  const addressList = {
    56: "0x9A4f119904C6926EFE76F64BB70E35198a4dFF5f",
    42161: "0x772Fc767b6ff9841175922dE11DCaF9365091d2C"
  }
  const chainIdList = {
    56: 56,
    42161: 42161
  }
  const chainId = chainIdList[wallet.detail.chainId] ? chainIdList[wallet.detail.chainId] : 42161
  const address = addressList[chainId] ? addressList[chainId] : addressList[chainId]
  console.log(chainId)
  const RebateContract = useContract("RebateImplementation", chainId, address)
  const token = chainId === 42161 ? "USDC" : "BUSD"
  const transactionTitle = {
    text: "Claim Reward",
    icon: token
  }
  const transactionContent = {
    success: "Claim Reward",
    error: "Transaction Failed"
  }
  const onCopy = useCallback((code) => {
    let text = accountType === "broker" ? `https://misc.deri.io/?ref=${code}&chainId=${chainId}` : code
    console.log("onCopy", navigator)
    navigator.clipboard.writeText(text);
    setIsCopied(true)
    setTimeout(() => setIsCopied(false), 5000);
  }, [accountType,chainId])

  const claim = useCallback(async () => {
    if (!bg(unclaimed).gt(0)) {
      return false
    }
    if (RebateContract) {
      setIsClaiming(true)
      let res
      try {
        if (accountType === "broker") {
          res = await RebateContract.claimBrokerRebate()
        } else {
          res = await RebateContract.claimRecruiterRebate()
        }
      } catch (error) {
        setIsClaiming(false)
        return false;
      }
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: transactionTitle, content: transactionContent, hash: res.hash
        })
      } else {
        setIsClaiming(false)
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            getClaimed()
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: true, hash: hash, error: "error" } })
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: false.success, hash: hash, error: "error" } })
          }
        }
      }
      setIsClaiming(false)
    }
  }, [unclaimed, accountType, RebateContract])

  const getClaimed = useCallback(async () => {
    if (RebateContract && wallet.detail.account) {
      let res;
      if (accountType === "broker") {
        res = await RebateContract.brokerClaimed(wallet.detail.account)
        if (res) {
          res = BigNumber.from(res._hex).toString() / (10 ** 18)
          setClaimed(res)
        }
      } else {
        res = await RebateContract.recruiterClaimed(wallet.detail.account)
        if (res) {
          res = BigNumber.from(res._hex).toString() / (10 ** 18)
          setClaimed(res)
        }
      }
    }
  }, [RebateContract, wallet.detail.account, accountType])

  const getBrokerInfos = useCallback(async () => {
    if (RebateContract && wallet.detail.account) {
      let res
      if (accountType === "broker") {
        res = await RebateContract.brokerInfos(wallet.detail.account);
      } else {
        res = await RebateContract.recruiterInfos(wallet.detail.account);
      }
      if (res) {
        let obj = {
          code: res.code,
          id: res.id,
        }
        setBrokerInfos(obj)
      }
    }
  }, [RebateContract, wallet.detail.account, accountType, infosRef.current])

  const getTotalFee = useCallback(async () => {
    if (RebateContract && wallet.detail.account) {
      let res
      if (accountType === "broker") {
        res = await RebateContract.brokerFees(wallet.detail.account)
      } else {
        res = await RebateContract.recruiterFees(wallet.detail.account)
      }
      if (res) {
        res = BigNumber.from(res._hex).toString() / (10 ** 18)
        let explained = ''
        if (res >= 10 && res < 1000) {
          explained = 'one'
        } else if (res >= 1000 && res < 2000) {
          explained = 'two'
        } else if (res >= 2000 && res < 4000) {
          explained = 'three'
        } else if (res >= 4000 && res < 6000) {
          explained = 'four'
        } else if (res >= 6000 && res < 10000) {
          explained = 'five'
        } else if (res >= 10000) {
          explained = 'six'
        }
        setExplained(explained)
        setTotalFee(res)
      }
    }
  }, [RebateContract, wallet.detail.account, accountType])

  const registerAfter = () => {
    getBrokerInfos()
    getBrokerRebate()
    getTotalFee()
    getClaimed()
  }


  const ShowRegister = useCallback(() => {
    if (wallet.detail.account && RebateContract) {
      // show(<Register account={wallet.detail.account} registerAfter={registerAfter} contract={RebateContract} accountType={accountType} />, {
      //   key: "register",
      //   fading: true,
      //   style: {
      //     background: "rgba(0, 0, 0, 0.5)",
      //     zIndex: 2,
      //   },
      // })
      openRegisterModal()
    }
  }, [wallet.detail.account, RebateContract, accountType])

  const getTotalUsers = useCallback(async () => {
    if (wallet.detail.account) {
      let res = await axios.get("https://referral.deri.io/referral_count", {
        params: {
          chainId: wallet.detail.chainId,
          account: wallet.detail.account,
          role: accountType
        },
      })
      if (res.data) {
        setTotalUser(res.data.count)
      }
    }
  }, [accountType, wallet.detail.account, wallet.detail.chainId])

  const getBrokerRebate = useCallback(async () => {
    if (RebateContract && wallet.detail.account) {
      let res
      if (accountType === "broker") {
        res = await RebateContract.getBrokerRebate(wallet.detail.account);
      } else {
        res = await RebateContract.getRecruiterRebate(wallet.detail.account)
      }
      if (res) {
        console.log("getBrokerRebate", res)
        let total = accountType === "broker" ? res.totalBrokerRebate._hex : res.totalRecruiterRebate._hex
        let crrent = accountType === "broker" ? res.currentBrokerRate._hex : res.currentRecruiterRate._hex
        let obj = {
          currentRate: BigNumber.from(crrent).toString() / (10 ** 18),
          totalRebate: BigNumber.from(total).toString() / (10 ** 18)
        }
        setReBateInfo(obj)
      }
    }
  }, [RebateContract, wallet.detail.account, accountType])

  const getUpdatedTime = useCallback(async () => {
    if (RebateContract) {
      let res = await RebateContract.updatedTimestamp();
      if (res) {
        res = BigNumber.from(res._hex).toString() / (10 ** 6)
        res = res * 1000
        let date = new Date(res)
        let year = date.getUTCFullYear()
        let month = date.getUTCMonth() + 1
        let day = date.getUTCDate()
        let hour = date.getUTCHours()
        let minute = date.getUTCMinutes()
        let second = date.getUTCSeconds()
        if (month < 10) {
          month = `0${month}`
        }
        if (day < 10) {
          day = `0${day}`
        }
        if (hour < 10) {
          hour = `0${hour}`
        }
        if (minute < 10) {
          minute = `0${minute}`
        }
        if (second < 10) {
          minute = `0${minute}`
        }
        let text = ` ${month}.${day}.${year} ${hour}:${minute}:${second}`
        setUpdatedTime(text)
      }
    }
  }, [RebateContract])

  const switchType = useCallback(() => {
    if (accountType === "broker") {
      setAccountType("broker_recruiter")
    } else {
      setAccountType("broker")
    }
  }, [accountType])

  useEffect(() => {
    if (RebateContract && wallet.detail.account && wallet.detail.chainId && wallet.detail.chainId === chainId) {
      console.log(wallet.detail.chainId, chainId)
      getBrokerInfos()
      getBrokerRebate()
      getTotalFee()
      getClaimed()
    }
  }, [RebateContract, wallet.detail.account, accountType, wallet.detail.chainId, chainId])

  useEffect(() => {
    if (reBateInfo.totalRebate) {
      let unclaimed = bg(reBateInfo.totalRebate).minus(claimed).toNumber()
      setUnclaimed(unclaimed)
    } else {
      setUnclaimed(0)
    }

  }, [claimed, reBateInfo])

  useEffect(() => {
    if (RebateContract) {
      getUpdatedTime()
    }
  }, [RebateContract])

  useEffect(() => {
    if (wallet.detail.account) {
      getTotalUsers()
    }
  }, [wallet.detail.account, wallet.detail.chainId, accountType])
  return <div className='rebate-box'>
    <div className='rebate-box-info'>
      <div className='rebate-box-info-titile'>
        <span>
          Referrals
        </span>
        <span className='rebate-des'>
          Earn rebates through the DERI referral program.
          <br></br>
          For more information, please read the
          <a href='https://deri-protocol.medium.com/introducing-the-deri-protocol-referral-program-a9341a127c69' target='_blank' rel='noreferrer'>
            {" "} referral program details.
          </a>
        </span>
      </div>
      <div className='rebate-box-header'>
        <div className='rebate-avatar'>
          <Icon token="rebate" />
          {explained ? <Icon token={`explained-${explained}`} className="explained-logo" /> : null}
        </div>
        <div className='rebate-account-reward-info'>
          <div className='rebate-reward'>
            You have <span className={classNames('reward-btn', {
              "no-reward": unclaimed === 0
            })} onClick={claim} >
              {isClaiming && <Spinner />}
              {unclaimed.toFixed(2)} <span className='token-icon-reward'><Icon token={token} /></span></span> unclaimed {token} reward
          </div>
          <div className='rebate-code'>
            Referral Code
            {brokerInfos.code ? <div className='code-hash-copy'>
              <div className='code-hash'>
                {brokerInfos.code}
                <Icon token="copy-code" className={classNames({ "copy-code-pc": isCopied })} onClick={() => onCopy(brokerInfos.code)} />
                {isCopied ? <div className='copied-box copied-m'>
                  <Icon token="copied-logo" />
                  Copied
                </div> : null}
              </div>
              {isCopied ? <div className='copied-box copied-pc'>
                <Icon token="copied-logo" />
                Copied
              </div> : null}
            </div> : accountType === "broker" ? <div className={classNames('no-code-hash', {
              "no-wallet": !wallet.isConnected()
            })} onClick={ShowRegister}>
              Generate  Code
            </div> : <a className='no-recruiter-code-hash' target='_blank' href='https://forms.gle/BRWmZbzbexG9L1LJ6' rel='noreferrer' >
              <Icon token="go-recruiter" /> Apply
            </a>}
          </div>
        </div>
        <div className='broker-recruiter' onClick={switchType}>
          <div className={classNames({
            'broker-type': accountType === "broker",
            'recruiter-type': accountType !== "broker",
          })} >
            {accountType === "broker" ? "Broker" : <>
              Broker
              <br></br>
              Recruiter</>}
          </div>
        </div>
      </div>
      <div className='rebate-box-use-info'>
        <div className='rebate-box-user-info-header'>
          <div className='rebate-box-user-info-title'>
            My Referral Stats <span className='rebate-box-user-info-title-time-pc'>
              （Last Updated: {updatedTime} UTC）
            </span>
            <span className='rebate-box-user-info-title-time-m'>
              Last Updated: {updatedTime} UTC
            </span>
          </div>
          {/* {wallet.isConnected()?<div className='wallet-account-box'>
            <Icon token={curConnector.icon} width='20' />
            {formatAddress(wallet.detail.account)}
          </div>: <Button label="CONNECT WALLET" className='m-connect' onClick={showWalletModal} width={192} height={48} fontWeight={700} fontColor='#FFF' fontSize='18' />} */}
          <WalletView lang={lang} isShowWalletModal={false} />
        </div>
        <div className='rebate-box-use-info-box'>
          <div className='total-rebate-bg'>
            <div className='total-rebate-box'>
              <Icon token="rebate-total-users" />
              <div className='total-rebate-box-info'>
                <div className='total-rebate-box-info-num'>
                  <DeriNumberFormat value={totalUser} />
                </div>
                <div className='total-rebate-box-info-title'>
                  Total Users
                </div>
              </div>
            </div>
          </div>
          <div className='total-rebate-bg'>
            <div className='total-rebate-box total-fee-box'>
              <Icon token="rebate-total-fee" />
              <div className='total-rebate-box-info'>
                <div className='total-rebate-box-info-num'>
                  <DeriNumberFormat value={totalFee} decimalScale={+totalFee === 0 ? 0 : 2} />
                </div>
                <div className='total-rebate-box-info-title'>
                  Total Transaction Fee
                </div>
              </div>
            </div>
          </div>
          <div className='total-rebate-bg'>
            <div className='total-rebate-box total-rewards-box'>
              <Icon token="rebate-total-rewards" />
              <div className='total-rebate-box-info'>
                <div className='total-rebate-box-info-num'>
                  <DeriNumberFormat value={reBateInfo.totalRebate} decimalScale={+reBateInfo.totalRebate === 0 ? 0 : 2} />
                </div>
                <div className='total-rebate-box-info-title'>
                  Total Rewards
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className='rebate-box-explained'>
        <div className='rebate-box-explained-title'>
          Tiers Explained <a href='https://deri-protocol.medium.com/introducing-the-deri-protocol-referral-program-a9341a127c69' target='_blank' rel='noreferrer'>
            <Icon token="rebate-warn" />
          </a>
        </div>
        <div className='rebate-box-explained-list'>
          <div className='rebate-box-explained-list-box rebate-explained-margin-box'>
            <div className='explained-box'>
              <div className='explained-box-info explained-one'>
                <div className='explained-logo'>
                  <Icon token="explained-one" />
                </div>
                <div className='explained-text'>
                  <div className='explained-rate'>
                    20% commission rate
                  </div>
                  <div className='explained-range-usd'>
                    For transaction fee range of 10-1000 USD
                  </div>
                </div>
              </div>
            </div>
            <div className='explained-box'>
              <div className='explained-box-info explained-two'>
                <div className='explained-logo'>
                  <Icon token="explained-two" />
                </div>
                <div className='explained-text'>
                  <div className='explained-rate'>
                    20% - 24% commission rate
                  </div>
                  <div className='explained-range-usd'>
                    For transaction fee range of 1000-2000 USD
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='rebate-box-explained-list-box'>
            <div className='explained-box'>
              <div className='explained-box-info explained-three'>
                <div className='explained-logo'>
                  <Icon token="explained-three" />
                </div>
                <div className='explained-text'>
                  <div className='explained-rate'>
                    24% - 28% commission rate
                  </div>
                  <div className='explained-range-usd'>
                    For transaction fee range of 2000-4000 USD
                  </div>
                </div>
              </div>
            </div>
            <div className='explained-box'>
              <div className='explained-box-info explained-four'>
                <div className='explained-logo'>
                  <Icon token="explained-four" />
                </div>
                <div className='explained-text'>
                  <div className='explained-rate'>
                    28% - 32% commission rate
                  </div>
                  <div className='explained-range-usd'>
                    For transaction fee range of 4000-6000 USD
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='rebate-box-explained-list-box'>
            <div className='explained-box'>
              <div className='explained-box-info explained-five'>
                <div className='explained-logo'>
                  <Icon token="explained-five" />
                </div>
                <div className='explained-text'>
                  <div className='explained-rate'>
                    32%-40% commission rate
                  </div>
                  <div className='explained-range-usd'>
                    For transaction fee range of 6000-10000 USD
                  </div>
                </div>
              </div>
            </div>
            <div className='explained-box'>
              <div className='explained-box-info explained-six'>
                <div className='explained-logo'>
                  <Icon token="explained-six" />
                </div>
                <div className='explained-text'>
                  <div className='explained-rate'>
                    40% commission rate
                  </div>
                  <div className='explained-range-usd'>
                    For transaction fee range of 10000 USD and above
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
    <Icon token="copied-logo" className="loaded-copied-logo" />
    <RegisterModal>
      <Register closeRegisterModal={closeRegisterModal} contract={RebateContract} registerAfter={registerAfter} />
    </RegisterModal>
  </div>
}
export default inject('wallet', "intl")(observer(Rebate))